.bulldozer {
  position: fixed;
  top: 0px;
  right: 0px;
  height: 100%;
  background: white;
  border: black dashed 1px;
  z-index: 1000;
}
.bulldozer .dashboard__grid {
  width: 320px;
  border: none;
}
/*# sourceMappingURL=main.css.map */
demo-root {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  min-height: 100vh;
}
demo-root nav {
  margin-top: 20px;
  margin-bottom: 20px;
}
demo-root nav a {
  border: 1px solid #AAACAE;
  border-radius: 4px;
  background-color: #fff;
  color: #1373B6;
  cursor: pointer;
  padding: 4px 14px;
  text-decoration: none;
}
demo-root nav .toggleable {
  background-color: #AAACAE;
}
demo-root nav .toggleable::before {
  content: '\2610';
  padding-right: 0.25rem;
}
demo-root nav .toggleable.selected {
  background-color: white;
  border: 2px solid black;
}
demo-root nav .toggleable.selected::before {
  content: '\2611';
}
demo-root nav a:hover {
  background-color: #AAACAE;
}
demo-root nav .rot-90 {
  display: inline-block;
  transform: rotateZ(100deg);
  font-weight: bold;
}
demo-root .colSpanDemo {
  display: flex;
}
demo-root .colSpanDemo > muuri-grid {
  flex-basis: 932px;
}
demo-root .colSpanDemo .instructions {
  margin-left: 20px;
  background: ghostwhite;
  border: 1px dashed black;
  padding: 5px;
}
.page-header {
  height: 100px;
  background-color: #332aaf;
  flex-grow: 0;
  flex-shrink: 0;
  color: white;
}
.page-header h1 {
  margin-top: 0;
}
@keyframes progress {
  from {
    width: 0%;
  }
  to {
    width: 100%;
  }
}
.demo__loading {
  min-height: 40vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.demo__loading .demo__progress {
  border: 1px solid black;
  padding: 2px;
  margin-bottom: 1rem;
  height: 2rem;
  width: 25rem;
  display: flex;
  align-items: stretch;
  justify-content: flex-start;
}
.demo__loading .demo__progress-bar {
  background-color: #346a97;
  animation-name: progress;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
/*# sourceMappingURL=main.css.map */
