.dashboard__grid .item {
  display: block;
  position: absolute;
  margin-left: 15px;
  margin-top: 20px;
  z-index: 1;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
}
.dashboard__grid .item.muuri-item-dragging {
  z-index: 20000;
}
.dashboard__grid .item.muuri-item-releasing {
  z-index: 2;
}
.dashboard__grid .item.muuri-item-hidden {
  z-index: 0;
}
.dashboard__grid .item-content {
  position: relative;
  width: 100%;
  height: 100%;
}
.dashboard__grid .hXS {
  height: 70px;
}
.dashboard__grid .hXS .item-content::before {
  content: 'x-small';
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  padding: 0 10px;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  border-bottom-left-radius: 6px;
}
.dashboard__grid .hSM {
  height: 145px;
}
.dashboard__grid .hSM .item-content::before {
  content: 'small';
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  padding: 0 10px;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  border-bottom-left-radius: 6px;
}
.dashboard__grid .hMD {
  height: 256px;
}
.dashboard__grid .hMD .item-content::before {
  content: 'medium';
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  padding: 0 10px;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  border-bottom-left-radius: 6px;
}
.dashboard__grid .hLG {
  height: 322px;
}
.dashboard__grid .hLG .item-content::before {
  content: 'large';
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  padding: 0 10px;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  border-bottom-left-radius: 6px;
}
.dashboard__grid .hXL {
  height: 508px;
}
.dashboard__grid .hXL .item-content::before {
  content: 'x-large';
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  padding: 0 10px;
  border-left: 2px solid white;
  border-bottom: 2px solid white;
  border-bottom-left-radius: 6px;
}
.dashboard__grid .wS {
  width: 290px;
}
.dashboard__grid .wM {
  width: 595px;
}
.dashboard__grid .wL {
  width: 900px;
}
.dashboard__grid .dashboard__height-demo-subject {
  background-color: lightblue;
  height: 10px;
  transition: height 0.5s ease-in-out;
}
.dashboard__grid .dashboard__height-demo-subject::before {
  display: block;
  text-align: center;
  content: 'Hover to expand';
  font-size: 10px;
  line-height: 8px;
  color: black;
}
.dashboard__grid .dashboard__height-demo-subject:hover {
  height: 150px;
}
